import * as React from 'react';
import { Box, Avatar, Stack, Container } from '@chakra-ui/react';
import {Link} from "react-router-dom"
import "./Navbar.css"

function Navbar(){
    let [homeColor,setHomeColor] = React.useState("colorLink")
    let [aboutColor,setAboutColor] = React.useState("Link")
    let [skillsColor,setSkillsColor] = React.useState("Link")
    let [resumeColor,setResumeColor] = React.useState("Link")
    let [contactColor,setContactColor] = React.useState("Link")
    function changeHomeColor(){
        setHomeColor("colorLink")
        setAboutColor("Link")
        setSkillsColor("Link")
        setResumeColor("Link")
        setContactColor("Link")
    }
    function changeAboutColor(){
        setHomeColor("Link")
        setAboutColor("colorLink")
        setSkillsColor("Link")
        setResumeColor("Link")
        setContactColor("Link")
    }
    function changeSkillsColor(){
        setHomeColor("Link")
        setAboutColor("Link")
        setSkillsColor("colorLink")
        setResumeColor("Link")
        setContactColor("Link")
    }
    function changeResumeColor(){
        setHomeColor("Link")
        setAboutColor("Link")
        setSkillsColor("Link")
        setResumeColor("colorLink")
        setContactColor("Link")
    }
    function changeContactColor(){
        setHomeColor("Link")
        setAboutColor("Link")
        setSkillsColor("Link")
        setResumeColor("Link")
        setContactColor("colorLink")
    }
return(
    <Container pos="fixed" width="100vw" backgroundColor="black">
    <Box display="flex" justifyContent="space-between" padding="25px" margin="0 30px" fontSize="22px" fontWeight="500">
    <Avatar src='https://img.icons8.com/fluency/344/portfolio.png' sx={{width:"40px",height:"40px"}}  className='App'></Avatar>
    <Stack color="white" direction="row" gap="35px">
        <Box><Link to="/" className={homeColor} onClick={changeHomeColor}>Home</Link></Box>
        <Box><Link to="/about" className={aboutColor} onClick={changeAboutColor}>About</Link></Box>
        <Box><Link to="/skills" className={skillsColor} onClick={changeSkillsColor}>Skills</Link></Box>
        <Box><Link to="/resume" className={resumeColor} onClick={changeResumeColor}>Resume</Link></Box>
        <Box><Link to="/contact" className={contactColor} onClick={changeContactColor}>Contact</Link></Box>
    </Stack>
    </Box>
    </Container>
)
}

export default Navbar