import { Box, Button, Img, Text } from "@chakra-ui/react"
import React from "react"
import {GoMarkGithub} from "react-icons/go"

function Home(){
    let Colors = ['creamson','red','yellow','green',"tan",'skyblue',"tomato","orange"]
    let [color,setColor] = React.useState("white")
    let [index,setIndex] = React.useState(0)
    React.useEffect(()=>{
        let x = setInterval(()=>{
            setColor(Colors[index])
            setIndex(index++)
            if(index===8){
                index = 0
            }
        },1000)
        let cleanup = () => clearInterval(x)
        return cleanup
    },[])
    
    return(
        <Box width="70vw" height="500px" padding="20px" lineHeight={1} textAlign="left" color="white" margin="auto" marginTop="170px">
            <Text fontSize="18px" color="teal" fontWeight="bold">Hello everyone, My name is</Text>
            <Text fontSize="80px" margin="0px 0px 0px -5px" fontWeight={600} letterSpacing={3}>Pranay Khadse</Text>
            <Text fontSize="25px" letterSpacing={11} color={color} fontWeight={700}>Full Stack Web Developer</Text>
            <Button bg="#FF5733" marginTop="30px" color="white" fontSize="20px" borderRadius="10px" width="120px" height="50px"><GoMarkGithub/>..GitHub</Button>
        </Box>
    )
}

export default Home